import { useState, useCallback, useEffect } from 'react';
import { GameState, Bear, CapturedAlien } from '../types';
import { 
  locations, 
  thoughts, 
  createBearRecruitment, 
  alienComments, 
  spaceExplorationTexts,
  INITIAL_MESSAGE,
  locationInitial,  // 新しく追加
} from '../data/eventData';
import { recruitmentResults } from '../data/eventData';
import { generateRareItem, generateSpecialMenu } from '../utils/itemGenerator';
import { cafeReport, CafeReportResult } from '../utils/cafeReport'; // CafeReportResult をインポート



// ログメッセージの新しい型を定義
type LogMessage = {
  message: string;
  category: 'cafeReport' | 'bearRecruitment' | 'alienEncounter' | 'specialEvent' | 'thought' | 'exploration'| 'initial';
};




//ストア数で宇宙空間location移動 最初のメッセージなど
export const useRandomEvent = (
  gameState: GameState,
  updateGameState: (newState: Partial<GameState> | ((prevState: GameState) => Partial<GameState>)) => void,
  updateLocation: (location: { emoji: string; name: string }) => void
) => {
  const [logs, setLogs] = useState<LogMessage[]>(() => 
    gameState.logs.length > 0 ? gameState.logs : [{
      message: INITIAL_MESSAGE,
      category: 'initial'
    }]
  );

  const addLogMessage = useCallback((message: string, category: LogMessage['category']) => {
    setLogs(prevLogs => {
      const newLog: LogMessage = { message, category };
      const newLogs = [...prevLogs, newLog].slice(-50);
      updateGameState(prevState => ({ ...prevState, logs: newLogs }));
      return newLogs;
    });
  }, [updateGameState]);


  //宇宙空間移動関連


  // 新しく追加: latestExploration の状態管理
  const [latestExploration, setLatestExploration] = useState<string | null>(null);

  const changeLocation = useCallback(() => {
    let newLocation;
    if (gameState.storeCount === 1) {
      // 初期状態の設定
      // 注意: locationInitialは配列なので、最初の要素を使用
      newLocation = locationInitial[0]; // locationInitialから直接選択（新しいプロパティを含む）
    } else {
      // ストア数が2以上の場合、全てのロケーションからランダムに選択
      const randomIndex = Math.floor(Math.random() * locations.length);
      newLocation = locations[randomIndex]; // locationsから直接選択（新しいプロパティを含む）
    }
    
    // 注意: updateLocationは変更なし。新しいプロパティを含む完全なlocationオブジェクトを渡す
    updateLocation(newLocation);
    
    // 以下の部分は変更なし
    const textTemplate = spaceExplorationTexts[Math.floor(Math.random() * spaceExplorationTexts.length)];
    const explorationText = textTemplate
      .replace('{locationName}', newLocation.name)
      .replace('{vehicleName}', gameState.config.vehicleName);
    
    addLogMessage(explorationText, 'exploration');
    setLatestExploration(explorationText);
  
    return newLocation;
  }, [gameState.storeCount, gameState.config.vehicleName, updateLocation, addLogMessage]);
  
  // 注意: triggerRandomEventなど、他の関数は変更していません
  // currentLocationを使用している他の部分も、新しいプロパティを自動的に含むようになりました

  


  // 宇宙移動関係ここまで





  // 宇宙人関連
  const generateAlienComment = useCallback((alienType: string, locationName: string, locationEmoji: string) => {
    const comment = alienComments[Math.floor(Math.random() * alienComments.length)];
    return `🤔研究メモ：「 ${comment}」`;
  }, []);

  const captureAlien = useCallback((locationName: string, locationEmoji: string): CapturedAlien => {
    const aliens = ['👾', '🥚', '💎', '🦖', '🐬', '🛸'];
    const alienType = aliens[Math.floor(Math.random() * aliens.length)];
    const comment = generateAlienComment(alienType, locationName, locationEmoji);
    
    const newAlien: CapturedAlien = {
      type: alienType,
      locationName,
      locationEmoji,
      comment
    };
    updateGameState((prevState: GameState) => ({
      capturedAliens: [...prevState.capturedAliens, newAlien]
    }));
    return newAlien;
  }, [updateGameState, generateAlienComment]);

  // 宇宙人戦闘Event
  const alienEncounter = useCallback(() => {
    if (gameState.researchFacilityUnlocked && gameState.combatPowerLevel > 0) {
      const currentLocation = locations[Math.floor(Math.random() * locations.length)];
      const alienName = `${currentLocation.name}宇宙人`;
      addLogMessage(`${currentLocation.emoji} ${alienName}と遭遇しました！`, 'alienEncounter');
      
      if (Math.random() < 0.6 + (gameState.combatPowerLevel * 0.1)) {
        const capturedAlien = captureAlien(currentLocation.name, currentLocation.emoji);
        addLogMessage(`😊戦闘に勝利し、${capturedAlien.locationEmoji} ${alienName} (${capturedAlien.type}型)を捕獲しました！`, 'alienEncounter');
        addLogMessage(capturedAlien.comment, 'alienEncounter');
        updateGameState(prevState => ({
          ...prevState,
          combatPowerLevel: prevState.combatPowerLevel + 1
        }));
      } else {
        addLogMessage("🥹戦闘に敗れてしまいました。次は勝てるよう、さらなる研究が必要です。", 'alienEncounter');
      }
    } else if (gameState.researchFacilityUnlocked) {
      addLogMessage("💡宇宙人の気配を感じましたが、まだ戦う準備ができていません。", 'alienEncounter');
    }
  }, [gameState.researchFacilityUnlocked, gameState.combatPowerLevel, addLogMessage, captureAlien, updateGameState]);

  // 宇宙人研究所ボタンモーダル内容 Modalconentsに移動



  // スペシャルEvent
  const specialEvent = useCallback(() => {
    const events = [
      "✨️錬金術の秘法を発見！カフェの売上が一時的に倍増します。",
      "✨️タイムマシンの試作機が完成！過去のベストセラーメニューを再現できるようになりました。",
      "✨️反物質コーヒーの開発に成功！エネルギー効率が劇的に向上します。",
      "✨️原始ボルツマン脳とコンタクト！宇宙の真理に触れた気がします。"
    ];
    const event = events[Math.floor(Math.random() * events.length)];
    addLogMessage(event,'specialEvent');
    // TODO: イベントの効果を実装
  }, [addLogMessage]);

  // 宇宙人研究所開設
  const openResearchFacility = useCallback(() => {
    const alchemyBearCount = Object.values(gameState.bearTypes).reduce((count, bear) => {
      if (bear.name.toLowerCase().includes('alchemy') || bear.name.toLowerCase().includes('錬金術')) {
        return count + bear.count;
      }
      return count;
    }, 0);
  
    if (alchemyBearCount >= 2) {
      const alchemyBearType = Object.values(gameState.bearTypes).find(bear => 
        bear.name.toLowerCase().includes('alchemy') || bear.name.toLowerCase().includes('錬金術')
      );
      
      if (alchemyBearType) {
        updateGameState(prevState => ({
          ...prevState,
          researchFacilityUnlocked: true,
          bearTypes: {
            ...prevState.bearTypes,
            [alchemyBearType.name]: {
              ...alchemyBearType,
              count: alchemyBearType.count - 1
            }
          },
          researchFacilityDirector: {
            ...alchemyBearType,
            count: 1
          },
          combatPowerLevel: prevState.combatPowerLevel + 1
        }));
        addLogMessage("🏰宇宙人研究所が開設され、錬金術ベアが所長に就任しました！戦闘力が上昇しました。",'specialEvent');
      }
    } else {
      addLogMessage("💡研究所を開設するには、錬金術ベアが2体以上必要です。",'specialEvent');
    }

  }, [gameState, updateGameState, addLogMessage, changeLocation]); // changeLocation を追加



  //宇宙空間移動（location）他

  const triggerRandomEvent = useCallback(() => {
    // 店舗数が増加したときのみロケーション変更を行う
    if (gameState.storeCount > gameState.prevStoreCount) { // newGameState を gameState に変更
      const newLocation = changeLocation();
      updateGameState(prevState => ({
        ...prevState,
        prevStoreCount: gameState.storeCount, // newGameState を gameState に変更
        currentLocation: newLocation,
      }));
    }
  





    // 宇宙人遭遇イベント　確率デフォは0.05　将来的にはなにかのステータス連動で確率アップ
    if (Math.random() < 0.05 && gameState.researchFacilityUnlocked) {
      alienEncounter();
    }

  // 店舗数増加のイベント
  const totalBears = Object.values(gameState.bearTypes).reduce((sum, bear) => sum + bear.count, 0);
  if (totalBears >= gameState.storeCount * 5) {
    const newStoreCount = gameState.storeCount + 1;
    updateGameState(prevState => ({ ...prevState, storeCount: newStoreCount }));
    addLogMessage(`✨️おめでとうございます！新しい店舗（${newStoreCount}号店）がオープンしました！銀河防衛の新たな拠点の完成です。`, 'specialEvent');
    if (newStoreCount <= locations.length) {
      addLogMessage(`✨️新しい宇宙空間「${locations[newStoreCount - 1].name}」への探索が可能になりました！`, 'specialEvent');
    }
    changeLocation(); // 新しい店舗オープン時に位置を更新
  }

  // ... 他のイベントコード

    // ベア遭遇イベント　※テスト中は確率をいじってるので注意　デフォは0,1-0.2 ★
    if (Math.random() < 0.1) {
      const bearRecruitment = createBearRecruitment(gameState.config);
      const bearTypes = Object.values(gameState.bearTypes).filter(bear => bear !== undefined);
      if (bearTypes.length > 0) {
        const bearType = bearTypes[Math.floor(Math.random() * bearTypes.length)];
        const situation = bearRecruitment.situations[Math.floor(Math.random() * bearRecruitment.situations.length)];
        const persuasion = bearRecruitment.persuasions[Math.floor(Math.random() * bearRecruitment.persuasions.length)];

        addLogMessage(situation, 'bearRecruitment');
        addLogMessage(`${bearType.emoji} ${bearType.name}があなたの前に現れました！`, 'bearRecruitment');
        addLogMessage(`😊あなた: "${persuasion}"`, 'bearRecruitment');

        const success = Math.random() < 0.5;
        updateGameState((prevState: GameState) => ({
          bearTypes: success
            ? {
                ...prevState.bearTypes,
                [bearType.name]: {
                  ...bearType,
                  count: (prevState.bearTypes[bearType.name]?.count || 0) + 1
                }
              }
            : prevState.bearTypes,
          latestBearRecruitment: {
            bearType,
            success,
            situation,
            persuasion,
            resultMessage: '' // 初期化
          }
        }));

        const resultMessages = success ? recruitmentResults.success : recruitmentResults.failure;
        const resultMessage = resultMessages[Math.floor(Math.random() * resultMessages.length)];
        const formattedResultMessage = resultMessage.replace('{bearType}', bearType.name);
        addLogMessage(formattedResultMessage, 'bearRecruitment');

        // 結果メッセージを保存
        updateGameState((prevState: GameState) => ({
          latestBearRecruitment: {
            ...prevState.latestBearRecruitment!,
            resultMessage: formattedResultMessage
          }
        }));
      }
    }


  

    // カフェレポートイベントを追加
    //確率設定　デフォは0.05-0.1 テスト上はあげる

    if (Math.random() < 0.05) { // 20%の確率でカフェレポートを生成
      const report = cafeReport(gameState);
      if (report !== null) {
        const reportLog = [
          `☕ Cafe Report:`,
          report.customerReaction,
          report.bearStatus,
          report.managerMessage,
          report.specialEvent
        ];
  
        reportLog.forEach(log => addLogMessage(log, 'cafeReport'));
  
        updateGameState(prevState => ({
          ...prevState,
          latestCafeReport: report
        }));
      }
    }


// ひとりごと、つぶやきイベント ※確率調整　0.2以下
if (Math.random() < 0.05) {
  const thought = thoughts[Math.floor(Math.random() * thoughts.length)];
  const thoughtMessage = `🤔あなたは宇宙の真理に思いを巡らせつぶやきます：「${thought}」`;
  addLogMessage(thoughtMessage, 'thought');
  updateGameState(prevState => ({
    ...prevState,
    latestThought: thoughtMessage
  }));
}


    // レアアイテムイベント
    if (Math.random() < 0.001) {
      const item = generateRareItem();
      if (!gameState.rareItemsDiscovered.includes(item)) {
        updateGameState((prevState: GameState) => ({
          rareItemsDiscovered: [...prevState.rareItemsDiscovered, item],
          money: prevState.money + 1000000
        }));
        addLogMessage(`✨️おや？あなたは${item}を発見しました！これは銀河防衛に役立ちそうです。`, 'specialEvent');
        addLogMessage('✨️レアアイテムの発見により$1,000,000を獲得しました！', 'specialEvent');
      }
    }
  
    // スペシャルメニューイベント
    if (Math.random() < 0.002 && Object.values(gameState.bearTypes).reduce((sum, bear) => sum + bear.count, 0) > 10) {
      const menu = generateSpecialMenu();
      if (!gameState.specialMenuItems.includes(menu)) {
        updateGameState((prevState: GameState) => ({
          specialMenuItems: [...prevState.specialMenuItems, menu]
        }));
        addLogMessage(`🧸🧸ベアたちの共同研究により、新しいスペシャルメニュー「${menu}」が開発されました！`, 'specialEvent');
      }
    }

    // 特別イベント
    if (Math.random() < 0.001) {
      specialEvent();
    }
  }, [gameState, updateGameState, addLogMessage, changeLocation]);



  return { 
    logs, 
    addLogMessage, 
    triggerRandomEvent, 
    openResearchFacility, 
    latestExploration, // 新しく追加: latestExploration を返す
  };
};