import React from 'react';
import { GameState, LogMessage } from '../types';
import { 
  getCafeReportContent, 
  getBearRecruitmentContent, 
  getSpecialMenuContent, 
  getRareItemsContent, 
  getAlienReportContent,
  getAlienResearchFacilityInfo  // 新しく追加 
} from './ModalContents';

interface ActionButtonsProps {
  gameState: GameState;
  updateGameState: (newState: Partial<GameState> | ((prevState: GameState) => Partial<GameState>)) => void;
  addLogMessage: (message: string, category: LogMessage['category']) => void;//修正
  openResearchFacility: () => void;
  showResearchFacilityInfo: () => void; // この行を変更
  setModalContent: (content: { title: string; content: string | JSX.Element } | null) => void;
}

const ActionButton: React.FC<{ onClick: () => void; children: React.ReactNode }> = ({ onClick, children }) => (
  <button
    onClick={onClick}
    className="
      px-3 py-1.5
      bg-amber-900 hover:bg-amber-800
      text-amber-100
      rounded-lg
      transition-all duration-200 ease-in-out
      backdrop-filter backdrop-blur-sm
      border border-amber-700
      shadow-md hover:shadow-lg
      text-xs font-medium
      flex items-center space-x-1
      hover:scale-105 transform
    "
  >
    {children}
  </button>
);

export const ActionButtons: React.FC<ActionButtonsProps> = ({
  gameState,
  updateGameState,
  addLogMessage,
  openResearchFacility,
  showResearchFacilityInfo,
  setModalContent
}) => {

  
  const showCafeReport = () => {
    const content = getCafeReportContent(gameState);
    setModalContent({ title: "Cafe Report", content });
  };

  const showBearRecruitment = () => {
    const content = getBearRecruitmentContent(gameState);
    setModalContent({ title: "Bear Recruitment", content });
  };

  const respondToCafe = () => {
    addLogMessage(`🚀${gameState.config.vehicleName}の通信機が壊れているようです…`, 'initial');
  };

  const showSpecialMenu = () => {
    const content = getSpecialMenuContent(gameState);
    setModalContent({ title: "Special Menu", content });
  };

  const showRareItems = () => {
    const content = getRareItemsContent(gameState);
    setModalContent({ title: "Rare Items", content });
  };

  const showResearchFacilityInfoModal = () => {
    setModalContent({
      title: "Alien Research Facility",
      content: getAlienResearchFacilityInfo(gameState)
    });
  };

  const showAlienReport = () => {
    const report = getAlienReportContent();
    setModalContent({ title: "Alien Research Report", content: report });
  };

  const totalBears = Object.values(gameState.bearTypes).reduce((sum, bear) => sum + bear.count, 0);
  const alchemyBearCount = Object.values(gameState.bearTypes).reduce((count, bear) => {
    if (bear.name.toLowerCase().includes('alchemy') || bear.name.toLowerCase().includes('錬金術')) {
      return count + bear.count;
    }
    return count;
  }, 0);

  return (
    <div className="bg-amber-50 bg-opacity-10 backdrop-filter backdrop-blur-lg rounded-xl p-4 shadow-lg mt-4 font-['VT323']">
      <div className="flex flex-wrap justify-center gap-2">
        <ActionButton onClick={showCafeReport}>☕ Cafe Report</ActionButton>
        <ActionButton onClick={respondToCafe}>📨 Respond to Cafe</ActionButton>
        {totalBears > 0 && <ActionButton onClick={showBearRecruitment}>🍯 Bear Recruitment</ActionButton>}
        {gameState.rareItemsDiscovered.length > 0 && <ActionButton onClick={showRareItems}>⚔️ Rare Items</ActionButton>}
        {gameState.specialMenuItems.length > 0 && <ActionButton onClick={showSpecialMenu}>🍬 Special Menu</ActionButton>}
        {gameState.researchFacilityUnlocked ? (
          <>
            <ActionButton onClick={showResearchFacilityInfoModal}>🏰 Alien Research Facility</ActionButton>
            <ActionButton onClick={showAlienReport}>👾 Alien Report</ActionButton>
          </>
        ) : (
          alchemyBearCount >= 2 && <ActionButton onClick={openResearchFacility}>🚀 Open Alien Research Facility</ActionButton>
        )}
      </div>
    </div>
  );
};