// EventLog.tsx

import React, { useRef, useEffect } from 'react';
import { GameState, LogMessage } from '../types'; // LogMessage をインポート



interface EventLogProps {
  logs: LogMessage[];
}


//イベントごとに色分け
//
const getCategoryStyle = (category: LogMessage['category']) => {
  const baseStyle = 'text-amber-100 border-y-0 border-r-0 border-l-4 text-xs transition-all duration-200 ease-in-out';
  //borderの数値は0、2、4、8のみ　※yはbt xはlr bg700は目立たせる
  switch (category) {
    case 'cafeReport':
      return `${baseStyle} bg-amber-900 hover:bg-amber-800 border-l-yellow-500`;
    case 'bearRecruitment':
      return `${baseStyle} bg-amber-900 hover:bg-amber-800 border-l-red-500`;
    case 'alienEncounter':
      return `${baseStyle} bg-amber-700 hover:bg-amber-600 border-l-lime-500`;
    case 'specialEvent':
      return `${baseStyle} bg-amber-700 hover:bg-amber-600 border-l-pink-500`;
    case 'thought':
      return `${baseStyle} bg-amber-900 hover:bg-amber-800 border-l-cyan-500`;
    case 'exploration':
      return `${baseStyle} bg-amber-700 hover:bg-amber-600 border-l-indigo-500`;
    default:
      return `${baseStyle} bg-amber-900 hover:bg-amber-800 border-l-amber-500`;
  }
};

export const EventLog: React.FC<EventLogProps> = ({ logs }) => {
  const logContainerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (logContainerRef.current) {
      logContainerRef.current.scrollTop = logContainerRef.current.scrollHeight;
    }
  }, [logs]);

  return (
    <div className="bg-gradient-to-br from-amber-900 to-amber-950 backdrop-filter backdrop-blur-sm rounded-xl p-4 shadow-lg border border-amber-700 font-['VT323']">
      <div 
        ref={logContainerRef}
        className="h-48 overflow-y-auto space-y-3 text-sm scrollbar-thin scrollbar-thumb-amber-600 scrollbar-track-amber-800/50"
        style={{
          scrollbarWidth: 'thin',
          scrollbarColor: '#d97706 #92400e80'
        }}
      >
        {logs.map((log, index) => (
          <p 
  key={index} 
  className={`py-1 px-2 mx-1 rounded ${getCategoryStyle(log.category)} hover:brightness-110`}
>
  {log.message}
</p>
        ))}
      </div>
    </div>
  );
};