import React, { useState, useEffect } from 'react';
import { flavorTexts, flavorTextsInitial } from '../data/eventData';
import { useGameState } from '../hooks/useGameState';

export const FlavorText: React.FC = () => {
  const [currentText, setCurrentText] = useState<string>('');
  const { gameState } = useGameState();
  const [isInitialPeriod, setIsInitialPeriod] = useState(true);

  useEffect(() => {
    const startTime = Date.now();
    
    const updateFlavorText = () => {
      const currentTime = Date.now();
      const elapsedTime = currentTime - startTime;
      const textsToUse = isInitialPeriod ? flavorTextsInitial : flavorTexts;
      const randomText = textsToUse[Math.floor(Math.random() * textsToUse.length)];
      setCurrentText(randomText);

      if (isInitialPeriod && elapsedTime > 3 * 60 * 1000) { // 3分経過
        setIsInitialPeriod(false);
      }
    };

    updateFlavorText();
    const interval = setInterval(updateFlavorText, 60000); // 1分ごとに更新

    return () => clearInterval(interval);
  }, [isInitialPeriod]);

  return (
    <div className="bg-amber-50 bg-opacity-10 backdrop-filter backdrop-blur-lg rounded-xl p-4 shadow-lg font-['VT323'] text-sm rounded-lg mb-4">
      <p className="italic text-xs">{currentText}</p>
    </div>
  );
};