import React, { useState } from 'react';
import { GameState } from '../types';
import { X } from 'lucide-react';
import { initialGameState } from '../utils/initialState';

interface SettingsModalProps {
  gameState: GameState;
  updateGameState: (newState: Partial<GameState>) => void;
  onClose: () => void;
}

export const SettingsModal: React.FC<SettingsModalProps> = ({ gameState, updateGameState, onClose }) => {
  const [vehicleName, setVehicleName] = useState(gameState.config.vehicleName);
  const [cafeName, setCafeName] = useState(gameState.config.cafeChainName);
  const [playerName, setPlayerName] = useState(gameState.config.playerName);

  const handleSave = () => {
    updateGameState({
      config: {
        ...gameState.config,
        vehicleName,
        cafeChainName: cafeName,
        playerName
      }
    });
    onClose();
  };

  const handleClearData = () => {
    if (window.confirm('Are you sure you want to delete all game data? This action cannot be undone.')) {
      localStorage.removeItem('quantumBearsGameState');
      updateGameState(initialGameState);
      setVehicleName(initialGameState.config.vehicleName);
      setCafeName(initialGameState.config.cafeChainName);
      setPlayerName(initialGameState.config.playerName);
      onClose();
    }
  };

  return (
    <div className="bg-amber-100 bg-opacity-90 backdrop-filter backdrop-blur-lg rounded-xl p-6 shadow-lg w-full max-w-md border border-amber-300 font-['VT323']">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-bold text-amber-900">Settings</h2>
        <button onClick={onClose} className="text-amber-700 hover:text-amber-500 transition-colors">
          <X size={24} />
        </button>
      </div>
      <div className="space-y-4">
        <div>
          <label className="block mb-2 text-amber-800">Player Name</label>
          <input
            type="text"
            value={playerName}
            onChange={(e) => setPlayerName(e.target.value)}
            className="w-full p-2 border rounded bg-amber-50 text-amber-900"
          />
        </div>
        <div>
          <label className="block mb-2 text-amber-800">Vehicle Name</label>
          <input
            type="text"
            value={vehicleName}
            onChange={(e) => setVehicleName(e.target.value)}
            className="w-full p-2 border rounded bg-amber-50 text-amber-900"
          />
        </div>
        <div>
          <label className="block mb-2 text-amber-800">Cafe Chain Name</label>
          <input
            type="text"
            value={cafeName}
            onChange={(e) => setCafeName(e.target.value)}
            className="w-full p-2 border rounded bg-amber-50 text-amber-900"
          />
        </div>
        <div className="flex justify-between pt-4">
          <button onClick={handleSave} className="bg-amber-600 hover:bg-amber-500 text-amber-100 px-4 py-2 rounded transition-colors">
            Save
          </button>
          <button onClick={handleClearData} className="bg-red-500 hover:bg-red-400 text-white px-4 py-2 rounded transition-colors">
            Clear Data
          </button>
        </div>
      </div>
    </div>
  );
};