import React, { useState } from 'react';
import { X, Settings, HelpCircle, Award } from 'lucide-react';
import { SettingsModal } from './SettingsModal';
import { HelpModal } from './HelpModal';
import { AchievementModal } from './AchievementModal';
import { GameState } from '../types';

// プロップスの型定義を更新
interface HamburgerMenuProps {
  gameState: GameState; // 追加: gameStateを受け取る
  updateGameState: (newState: Partial<GameState>) => void; // 追加: updateGameStateを受け取る
  gameConcept: string;
  buttonClassName?: string;
  children?: React.ReactNode;
}

export const HamburgerMenu: React.FC<HamburgerMenuProps> = ({
  gameState, // 追加: gameStateを受け取る
  updateGameState, // 追加: updateGameStateを受け取る
  gameConcept,
  buttonClassName,
  children
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const [activeModal, setActiveModal] = useState<'settings' | 'help' | 'achievements' | null>(null);

  const toggleMenu = () => setIsOpen(!isOpen);

  const openModal = (modal: 'settings' | 'help' | 'achievements') => {
    setActiveModal(modal);
    setIsOpen(false);
  };

  const closeModal = () => setActiveModal(null);

  return (
    <>
      <button
        onClick={toggleMenu}
        className={buttonClassName || "text-amber-100 hover:text-amber-200 transition-colors"}
      >
        {children || <span className="text-4xl">🍔</span>}
      </button>

      {isOpen && (
        <div className="absolute top-16 right-4 z-50">
          <div className="bg-amber-100 rounded-xl p-6 shadow-lg w-80">
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-2xl font-bold text-amber-900">Menu</h2>
              <button onClick={toggleMenu} className="text-amber-700 hover:text-amber-500 transition-colors">
                <X size={24} />
              </button>
            </div>
            <button
              onClick={() => openModal('settings')}
              className="block w-full text-left p-3 mb-2 text-amber-900 hover:bg-amber-200 rounded transition-colors"
            >
              ⚙️
              Settings
            </button>
            <button
              onClick={() => openModal('help')}
              className="block w-full text-left p-3 mb-2 text-amber-900 hover:bg-amber-200 rounded transition-colors"
            >
              🔰
              Help
            </button>
            <button
              onClick={() => openModal('achievements')}
              className="block w-full text-left p-3 text-amber-900 hover:bg-amber-200 rounded transition-colors"
            >
              🏆
              Achievements
            </button>
          </div>
        </div>
      )}

      {activeModal && (
        <div className="fixed inset-0 flex items-center justify-center z-50">
          {activeModal === 'settings' && (
            <SettingsModal
              gameState={gameState} // 更新: gameStateを渡す
              updateGameState={updateGameState} // 更新: updateGameStateを渡す
              onClose={closeModal}
            />
          )}
          {activeModal === 'help' && (
            <HelpModal
              onClose={closeModal}
              gameConcept={gameConcept}
            />
          )}
          {activeModal === 'achievements' && (
            <AchievementModal
              onClose={closeModal}
              gameState={gameState} // 更新: gameStateを渡す
            />
          )}
        </div>
      )}
    </>
  );
};