import React, { useState, useEffect } from 'react';
import { Bear } from '../types';
import { X } from 'lucide-react';

interface BearDetailModalProps {
  bear: Bear | null;
  onClose: () => void;
}

const ReportItem = ({ title, content, color, icon }: { title: string; content: string; color: string; icon: string }) => (
  <div className={`
    mb-4 p-4 rounded-lg ${color} shadow-md
    transition-all duration-200 ease-in-out
    hover:shadow-lg hover:brightness-110
    border-2
  `}>
    <div className="flex items-center mb-2">
      <span className="text-3xl mr-3">{icon}</span>
      <h3 className="text-xl font-bold text-amber-950 font-['Pacifico', cursive]">{title}</h3>
    </div>
    <p className="text-amber-900 text-sm font-['VT323', monospace] whitespace-pre-line">{content}</p>
  </div>
);

export const BearDetailModal: React.FC<BearDetailModalProps> = ({ bear, onClose }) => {
  const [selectedSpeciesTrait, setSelectedSpeciesTrait] = useState<string | null>(null);
  const [selectedCafeTrait, setSelectedCafeTrait] = useState<string | null>(null);

  useEffect(() => {
    if (bear) {
      setSelectedSpeciesTrait(getRandomTrait(bear.speciesTraits));
      setSelectedCafeTrait(getRandomTrait(bear.cafeTraits));
    }
  }, [bear]);

  if (!bear) return null;

  const getRandomTrait = (traits: string[]) => traits[Math.floor(Math.random() * traits.length)];



  return (
    <div className="fixed inset-0 bg-amber-900/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
      <div className="bg-gradient-to-br from-amber-100 to-amber-200 rounded-2xl p-8 shadow-2xl w-full max-w-2xl border border-amber-300 relative overflow-hidden">
        <div className="absolute inset-0 bg-opacity-10"></div>
        <div className="relative z-10">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-3xl font-bold text-amber-800 font-['Pacifico', cursive]">{bear.emoji} {bear.nameEn}</h2>
            <button onClick={onClose} className="text-amber-700 hover:text-amber-500 transition-colors">
              <X size={32} />
            </button>
          </div>
          <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
            <ReportItem
              title="Bear Info"
              content={`Name: ${bear.name} (${bear.nameEn})
Count: ${bear.count}
Power: ${bear.power.toLocaleString()}`}
              color="bg-blue-200 border-2 border-blue-300"
              icon="🐻"
            />
            <ReportItem
              title="Specialty"
              content={`${bear.specialty}\n\n${bear.description}`}
              color="bg-green-200 border-2 border-green-300"
              icon="🌟"
            />
            <ReportItem
              title="Skill"
              content={`${bear.skillName}`}
              color="bg-purple-200 border-2 border-purple-300"
              icon="⚡"
            />
            {selectedSpeciesTrait && (
              <ReportItem
                title="Species Trait"
                content={selectedSpeciesTrait}
                color="bg-yellow-200 border-2 border-yellow-300"
                icon="🧬"
              />
            )}
            {selectedCafeTrait && (
              <ReportItem
                title="Cafe Trait"
                content={selectedCafeTrait}
                color="bg-pink-200 border-2 border-pink-300"
                icon="☕"
              />
            )}
          </div>
        </div>
      </div>
    </div>
  );
};