import React from 'react';
import styled from 'styled-components';
import { 
  getCafeReportContent, 
  getBearRecruitmentContent, 
  getSpecialMenuContent, 
  getRareItemsContent, 
  getAlienReportContent,
  getAlienResearchFacilityInfo,
  getCafeStatusModalContent
} from '../../components/ModalContents';


const InfoBarContainer = styled.div`
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  z-index: 10;
`;

const InfoButton = styled.button`
  padding: 0.375rem 0.75rem;
  background-color: #78350f;
  color: #fef3c7;
  border-radius: 0.5rem;
  transition: all 200ms ease-in-out;
  backdrop-filter: blur(4px);
  border: 1px solid #92400e;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  font-size: 0.75rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.25rem;

  &:hover {
    background-color: #92400e;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    transform: scale(1.05);
  }
`;



interface InfoBarProps {
  day: number;
  vehicleName: string;
  location: string;
  onVehicleClick: () => void;
  onLocationClick: () => void;
  onDateClick: () => void;
  currentScene: 'space' | 'cafe';
  setModalContent: (content: { title: string; content: string | JSX.Element } | null) => void;
  gameState: any; // GameStateの型を適切に指定してください
}

export const InfoBar: React.FC<InfoBarProps> = ({
  day,
  vehicleName,
  location,
  onVehicleClick,
  onLocationClick,
  onDateClick,
  currentScene,
  setModalContent,
  gameState
}) => {
  const handleCafeClick = () => {
    const cafeContent = getCafeStatusModalContent(gameState);
    setModalContent({ title: "Cafe Status", content: cafeContent });
  };

  if (currentScene === 'space') {
    return (
      <InfoBarContainer>
        <InfoButton onClick={onVehicleClick}>🚀 {vehicleName}</InfoButton>
        <InfoButton onClick={onLocationClick}>📍 {location}宇宙空間</InfoButton>
      </InfoBarContainer>
    );
  } else {
    return (
      <InfoBarContainer>
        <InfoButton onClick={handleCafeClick}>☕ {gameState.config.cafeChainName}</InfoButton>
      </InfoBarContainer>
    );
  }
};