import React from 'react';
import { GameState } from '../types';
import { alienResearchReports } from '../data/eventData';
import { menuItems } from '../data/eventData'; // menuItemsをインポート

const ReportItem = ({ title, content, color, icon }: { title: string; content: string; color: string; icon: string }) => (
  <div className={`
    mb-4 p-4 rounded-lg ${color} shadow-md
    transition-all duration-200 ease-in-out
    hover:shadow-lg hover:brightness-110
    border-2
  `}>
    <div className="flex items-center mb-2">
      <span className="text-3xl mr-3">{icon}</span>
      <h3 className="text-xl font-bold text-amber-950 font-['Pacifico', cursive]">{title}</h3>
    </div>
    <p className="text-amber-900 text-sm font-['VT323', monospace] whitespace-pre-line">{content}</p>
  </div>
);



//カフェレポート起動直後
const getRandomTip = () => {
  const tips = [
    "宇宙の様々な場所でクオンタムベアを見つけ、カフェスタッフとして雇いましょう。彼らの特殊能力がカフェの成長を助けてくれます。",
    "各ベアタイプには独自の特性があります。彼らの能力を組み合わせることで、より効率的にカフェを運営できます。",
    "カフェの収益は自動的に宇宙船の改良や新しい装備の購入に使われます。より多くのベアを雇うことで、探索範囲が広がりますよ。",
    "時々、珍しいアイテムや特別なメニューを発見することがあります。これらは特別な効果をもたらすかもしれません。",
    "宇宙人との遭遇は避けられません。ベアたちの力を借りて、銀河の平和を守りましょう。"
  ];
  return tips[Math.floor(Math.random() * tips.length)];
};

const getInitialCafeReport = (playerName: string, cafeChainName: string): JSX.Element => {
  const randomTip = getRandomTip();
  
  return (
    <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
      <ReportItem
        title="Store Manager's Initial Report"
        content={`${playerName}様、${cafeChainName}の船長にしてオーナー様。宇宙の果てへの冒険の無事をお祈りしております。こちらカフェの状況をご報告いたします。`}
        color="bg-blue-200 border-2 border-blue-300"
        icon="📝"
      />
      <ReportItem
        title="Store Status"
        content="現在、1号店の運営は順調です。しかし、スタッフ不足で拡大が難しい状況です。宇宙の素晴らしいクオンタムベアたちとの出会いを期待しています。"
        color="bg-green-200 border-2 border-green-300"
        icon="🏠"
      />
      <ReportItem
        title="Advice"
        content={randomTip}
        color="bg-yellow-200 border-2 border-yellow-300"
        icon="💡"
      />
      <ReportItem
        title="Future Prospects"
        content={`${playerName}様の冒険が、${cafeChainName}の発展と銀河の平和につながることを願っています。素晴らしいクオンタムベアたちとの出会いを楽しみにしています。`}
        color="bg-purple-200 border-2 border-purple-300"
        icon="🚀"
      />
    </div>
  );
};

//カフェレポート ※カフェレポート（アクションボタン）とカフェステータス（カフェモードのボタンの方）は別なので注意

export const getCafeReportContent = (gameState: GameState): JSX.Element => {
  if (!gameState.latestCafeReport) {
    return (
      <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
        <ReportItem
          title="Cafe Report"
          content="No recent cafe report available."
          color="bg-gray-200 border-2 border-gray-300"
          icon="⚠️"
        />
      </div>
    );
  }

  const { revenue, customerReaction, bearStatus, managerMessage, specialEvent } = gameState.latestCafeReport;

  return (
    <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
      <ReportItem
        title="Revenue"
        content={revenue}
        color="bg-green-200 border-2 border-green-300"
        icon="💰"
      />
      <ReportItem
        title="Customer Reaction"
        content={customerReaction}
        color="bg-blue-200 border-2 border-blue-300"
        icon="😀"
      />
      <ReportItem
        title="Bear Staff Status"
        content={bearStatus}
        color="bg-yellow-200 border-2 border-yellow-300"
        icon="🐻"
      />
      <ReportItem
        title="Manager's Message"
        content={managerMessage}
        color="bg-purple-200 border-2 border-purple-300"
        icon="📣"
      />
      <ReportItem
        title="Special Event"
        content={specialEvent}
        color="bg-pink-200 border-2 border-pink-300"
        icon="🎉"
      />
    </div>
  );
};


//ベアリクルート

export const getBearRecruitmentContent = (gameState: GameState): JSX.Element => {
  if (gameState.latestBearRecruitment) {
    const { bearType, success, situation, persuasion, resultMessage } = gameState.latestBearRecruitment;
    const formattedResultMessage = resultMessage.replace('{bearType}', bearType.name);
    return (
      <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
        <ReportItem
          title="Bear Type"
          content={`${bearType.emoji} ${bearType.name} (${bearType.nameEn})`}
          color="bg-blue-200 border-2 border-blue-300"
          icon="🐻"
        />
        <ReportItem
          title="Situation"
          content={situation}
          color="bg-green-200 border-2 border-green-300"
          icon="💫"
        />
        <ReportItem
          title="Your Persuasion"
          content={persuasion}
          color="bg-yellow-200 border-2 border-yellow-300"
          icon="😊"
        />
        <ReportItem
          title="Result"
          content={`${success ? 'Success 🎉' : 'Failure 😢'} ${formattedResultMessage}`}
          color={success ? "bg-purple-200 border-2 border-rose-300" : "bg-red-200 border-2 border-red-300"}
          icon={success ? "🎊" : "❌"}
        />
      </div>
    );
  } else {
    return (
      <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
        <ReportItem
          title="Bear Recruitment"
          content="No bear recruitment event has occurred yet."
          color="bg-gray-200 border-2 border-gray-300"
          icon="⚠️"
        />
      </div>
    );
  }
};



