//宇宙人レポート
export const getAlienReportContent = (): JSX.Element => {
  const report = alienResearchReports[Math.floor(Math.random() * alienResearchReports.length)].getReport();
  
  // HTML文字列をパースしてオブジェクトに変換
  const parser = new DOMParser();
  const doc = parser.parseFromString(report, 'text/html');
  
  const title = doc.querySelector('h3')?.textContent || "Alien Research Report";
  const authors = doc.querySelector('p')?.innerHTML || "";
  const sections = Array.from(doc.querySelectorAll('h4')).map(h4 => ({
    title: h4.textContent || "",
    content: h4.nextElementSibling?.textContent || ""
  }));

  const colors = [
    "bg-red-200 border-2 border-red-300",
    "bg-blue-200 border-2 border-blue-300",
    "bg-green-200 border-2 border-green-300",
    "bg-yellow-200 border-2 border-yellow-300",
    "bg-purple-200 border-2 border-purple-300",
    "bg-pink-200 border-2 border-pink-300"
  ];

  return (
    <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
      <ReportItem
        title="Title"
        content={title}
        color={colors[0]}
        icon="🛸"
      />
      <ReportItem
        title="Authors"
        content={authors.replace(/<br>/g, '\n')}
        color={colors[1]}
        icon="🧸"
      />
      {sections.map((section, index) => (
        <ReportItem
          key={index}
          title={`Chapter ${index + 1}: ${section.title}`}
          content={section.content}
          color={colors[(index + 2) % colors.length]}
          icon="👾"
        />
      ))}
    </div>
  );
};


//宇宙人研究所　※userandomeventより移動

export const getAlienResearchFacilityInfo = (gameState: GameState): JSX.Element => {
  const alienCount = gameState.capturedAliens.length;
  const uniqueAliens = new Set(gameState.capturedAliens.map(alien => alien.type)).size;
  
  return (
    <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar text-amber-950">
      <ReportItem
        title="Alien Research Statistics"
        content={`Total captured aliens: ${alienCount}\nDiscovered alien species: ${uniqueAliens}`}
        color="bg-purple-200 border-2 border-purple-300"
        icon="🔬"
      />
      
      {alienCount > 0 && (
        <>
          <h2 className="text-2xl font-bold text-amber-950 mt-6 mb-2">Latest Captured Alien</h2>
          <ReportItem
            title="Location"
            content={`${gameState.capturedAliens[alienCount - 1].locationEmoji} ${gameState.capturedAliens[alienCount - 1].locationName}宇宙空間`}
            color="bg-blue-200 border-2 border-blue-300"
            icon="🚀"
          />
          <ReportItem
            title="Type"
            content={`${gameState.capturedAliens[alienCount - 1].type}型`}
            color="bg-green-200 border-2 border-green-300"
            icon="🧬"
          />
          <ReportItem
            title="Research Note"
            content={gameState.capturedAliens[alienCount - 1].comment}
            color="bg-yellow-200 border-2 border-yellow-300"
            icon="🤔"
          />
        </>
      )}
      
      {alienCount === 0 && (
        <ReportItem
          title="No Aliens Captured"
          content="You haven't captured any aliens yet. Keep exploring the universe!"
          color="bg-red-200 border-2 border-red-300"
          icon="🔍"
        />
      )}
    </div>
  );
};



