//カフェステータス ※カフェレポート（アクションボタン）とカフェステータス（カフェモードのボタンの方）は別なので注意




export const getCafeStatusModalContent = (gameState: GameState): JSX.Element => {
  const totalBears = Object.values(gameState.bearTypes).reduce((sum, bear) => sum + bear.count, 0);
  
  // ランダムにメニューアイテムを1つ選択
  const recommendedItem = menuItems[Math.floor(Math.random() * menuItems.length)];

  return (
    <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
      <ReportItem
        title={`${gameState.config.cafeChainName}`}
        content={`😊Owner: ${gameState.config.playerName}\n🏘️Stores: ${gameState.storeCount}\n🧸Total Bears: ${totalBears}`}
        color="bg-amber-200 border-2 border-amber-300"
        icon="☕"
      />
      <ReportItem
        title={` Recommendation!`}
        content={`${recommendedItem.item} ${recommendedItem.nameEN}: ${recommendedItem.menuName}\n🪙${recommendedItem.price}\n💡${recommendedItem.description}`}
        color="bg-pink-200 border-2 border-pink-300"
        icon="🐻"
      />
    </div>
  );
};

