import React, { useState, useEffect } from 'react';
import { GameStatus } from './components/GameStatus';
import { BearList } from './components/BearList';
import { EventLog } from './components/EventLog';
import { FlavorText } from './components/FlavorText';
import { GraphicArea } from './components/GraphicArea';
import { ActionButtons } from './components/ActionButtons';
import { BearDetailModal } from './components/BearDetailModal';
import { CafeModal } from './components/CafeModal';
import { useGameState } from './hooks/useGameState';
import { useRandomEvent } from './hooks/useRandomEvent';
import { Bear } from './types';
import { LogMessage } from './types';//tuika
import { SettingsModal } from './components/SettingsModal';
import { HelpModal } from './components/HelpModal';
import { Settings } from 'lucide-react';
import { getAlienResearchFacilityInfo } from './components/ModalContents';
import { getVehicleDetailsContent, getLocationDetailsContent, getDateModalContent } from './components/ModalContents';
//debug 
import { SimpleExplorationDisplay } from './components/SimpleExplorationDisplay';
// 追加: GraphicAreaManagerのインポート
import { GraphicAreaManager } from './components/GraphicArea/GraphicAreaManager';
import { HamburgerMenu } from './components/HamburgerMenu';

const gameConceptEnglish = "Quantum Bears' Cafe is an idle game where you manage a cosmic cafe chain with the help of quantum physics-inspired bears.";

export const App: React.FC = () => {
  const { gameState, updateGameState } = useGameState();
  const [currentLocation, setCurrentLocation] = useState({ emoji: '🌞', name: 'まだまだ太陽系の近く' });
  const { 
    logs, 
    addLogMessage, 
    triggerRandomEvent, 
    openResearchFacility, 
    latestExploration,
  } = useRandomEvent(gameState, updateGameState, setCurrentLocation);
  
  
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);
  const [isHelpOpen, setIsHelpOpen] = useState(false);
  const [selectedBear, setSelectedBear] = useState<Bear | null>(null);
  const [modalContent, setModalContent] = useState<{ title: string; content: string | JSX.Element } | null>(null);

 
  // 追加: 日付クリックハンドラ
  const handleDateClick = () => {
    const content = getDateModalContent(gameState);
    setModalContent({
      title: "Adventure Log",
      content: content
    });
  };

    // 追加: 車両と場所のモーダル表示状態
    const [showVehicleModal, setShowVehicleModal] = useState(false);
    const [showLocationModal, setShowLocationModal] = useState(false);
  
    // 追加: 車両クリックハンドラ
    const handleVehicleClick = () => {
      //console.log('handleVehicleClick called with:', { gameState, latestExploration });
      const content = getVehicleDetailsContent({...gameState, latestExploration});
      //console.log('Generated modal content:', content);
      setModalContent({
        title: "Vehicle Details",
        content: content
      });
    };
  

    //追加　locationハンドラ
  // 修正: 正しい handleLocationClick 定義
  const handleLocationClick = () => {
    setModalContent({
      title: "Location Details",
      content: getLocationDetailsContent({...gameState, latestExploration})
    });
  };
  

  const handleBearClick = (bear: Bear) => {
    setSelectedBear(bear);
  };

  const handleOpenSettings = () => {
    setIsSettingsOpen(true);
  };

  const handleOpenHelp = () => {
    setIsHelpOpen(true);
  };

  const showResearchFacilityInfo = () => {
    setModalContent({
      title: "Alien Research Facility",
      content: getAlienResearchFacilityInfo(gameState)
    });
  };


  useEffect(() => {
    const timer = setInterval(() => {
      triggerRandomEvent();
      updateGameState(prevState => ({ ...prevState, gameDay: prevState.gameDay + 1 }));
    }, 10000); // 10秒ごとに1日経過 

    return () => clearInterval(timer);
  }, [triggerRandomEvent, updateGameState]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-amber-900 via-amber-600 via-30% to-amber-950 to-75% text-amber-50 p-8 font-['VT323']">
      <div className="container mx-auto bg-amber-50 bg-opacity-10 backdrop-filter backdrop-blur-lg rounded-xl p-6 shadow-lg">
        <header className="flex justify-between items-center mb-4">
          <h1 className="text-4xl">🧸 Quantum Bears' Cafe ☕</h1>
          <HamburgerMenu
            gameState={gameState}
            updateGameState={updateGameState}
            gameConcept={gameConceptEnglish}
            buttonClassName="text-amber-100 hover:text-amber-200 transition-colors"
          >
            <span className="text-4xl">🍔</span>
          </HamburgerMenu>
        </header>
        <FlavorText />
        {/*grid colで幅を調整*/}
        <div className="grid grid-cols-7 gap-5">
          <div className="col-span-5 space-y-2">
            {/* 修正: GraphicAreaをGraphicAreaManagerに置き換え */}
            <GraphicAreaManager
              emoji={currentLocation.emoji}
              day={gameState.gameDay}
              vehicleName={gameState.config.vehicleName}
              location={currentLocation.name}
              onVehicleClick={handleVehicleClick}
              onLocationClick={handleLocationClick}
              onDateClick={handleDateClick}
              gameState={gameState} 
              setModalContent={setModalContent}  // この行を追加
            />

            <EventLog logs={logs as LogMessage[]} />

          </div>


          <div className="space-y-8 col-span-2">
            <BearList 
              bearTypes={gameState.bearTypes} 
              onBearClick={handleBearClick}
              storeCount={gameState.storeCount}
              totalBears={gameState.totalBears}
              cafeChainName={gameState.config.cafeChainName}
              playerName={gameState.config.playerName}
            />
          </div>
        </div>
        {/* アクションボタン*/}
        <div className="p-1">
        <ActionButtons 
          gameState={gameState} 
          updateGameState={updateGameState} 
          addLogMessage={addLogMessage}
          openResearchFacility={openResearchFacility}
          showResearchFacilityInfo={() => {}} // または完全に削除
          setModalContent={setModalContent}
        />
{/* debug用 <div className=''><SimpleExplorationDisplay latestExploration={latestExploration} /></div>*/}

        
        </div>



      </div>
      {selectedBear && (
        <BearDetailModal
          bear={selectedBear}
          onClose={() => setSelectedBear(null)}
        />
      )}
      {isSettingsOpen && (
        <SettingsModal
          gameState={gameState}
          updateGameState={updateGameState}
          onClose={() => setIsSettingsOpen(false)}
        />
      )}
      {isHelpOpen && (
        <HelpModal
          onClose={() => setIsHelpOpen(false)}
          gameConcept={gameConceptEnglish}
        />
      )}
      {modalContent && (
        <CafeModal
          title={modalContent.title}
          content={modalContent.content}
          onClose={() => setModalContent(null)}
        />
      )}
    </div>
  );
};

export default App;