import { GameState } from '../types';


export interface CafeReportResult {
  revenue: string;
  customerReaction: string;
  bearStatus: string;
  managerMessage: string;
  specialEvent: string;
}

// 初期レポートが表示されたかどうかを追跡するフラグ
let initialReportShown = false;


const getRandomTip = () => {
  const tips = [
    "宇宙の様々な場所でクオンタムベアを見つけ、カフェスタッフとして雇いましょう。彼らの特殊能力がカフェの成長を助けてくれます。",
    "各ベアタイプには独自の特性があります。彼らの能力を組み合わせることで、より効率的にカフェを運営できます。",
    "カフェの収益は自動的に宇宙船の改良や新しい装備の購入に使われます。より多くのベアを雇うことで、探索範囲が広がりますよ。",
    "時々、珍しいアイテムや特別なメニューを発見することがあります。これらは特別な効果をもたらすかもしれません。",
    "宇宙人との遭遇は避けられません。ベアたちの力を借りて、銀河の平和を守りましょう。"
  ];
  return tips[Math.floor(Math.random() * tips.length)];
};





const getInitialCafeReport = (playerName: string, cafeChainName: string): CafeReportResult => {
  const randomTip = getRandomTip();
  
  return {
    revenue: "$0",
    customerReaction: `🤖${playerName}様、${cafeChainName}の船長にしてオーナー様。宇宙の果てへの冒険の無事をお祈りしております。こちらカフェの状況をご報告いたします。`,
    bearStatus: "🤖現在、1号店の運営は順調です。しかし、スタッフ不足で拡大が難しい状況です。宇宙の素晴らしいクオンタムベアたちとの出会いを期待しています。",
    managerMessage: `🤖${randomTip}`,
    specialEvent: `🤖${playerName}様の冒険が、${cafeChainName}の発展と銀河の平和につながることを願っています。素晴らしいクオンタムベアたちとの出会いを楽しみにしています。`
  };
};

const getNoBearsReport = (): CafeReportResult => {
  return {
    revenue: "$0",
    customerReaction: "お客様はまだいません。",
    bearStatus: "ベアスタッフはまだいません。",
    managerMessage: "ベアスタッフを雇用することをお勧めします。",
    specialEvent: "特別なイベントはありません。"
  };
};


export const cafeReport = (gameState: GameState): CafeReportResult | null => {
  // ベアがいない場合
  if (Object.values(gameState.bearTypes).every(bear => bear.count === 0)) {
    if (!initialReportShown) {
      initialReportShown = true;
      return getInitialCafeReport(gameState.config.playerName, gameState.config.cafeChainName);
    } else {
      // 初期レポート表示後、ベアがいない間はnullを返す
      return null;
    }
  }

  // ベアがいる場合は通常のレポートを生成
  initialReportShown = false; // リセット（ベアが0になった時に再度初期レポートを表示するため）

  const totalPower = Object.values(gameState.bearTypes).reduce((sum, bear) => sum + bear.count * bear.power, 0);
  const baseProfitRate = 0.5 + (Math.random() * 0.5);
  const ambienceFactor = gameState.cafeAmbience / 100;
  const satisfactionFactor = gameState.customerSatisfaction / 100;
  const moraleFactor = gameState.bearMorale / 100;

  const profit = Math.floor(totalPower * baseProfitRate * ambienceFactor * satisfactionFactor * moraleFactor);

  return {
    revenue: `$${profit.toLocaleString()}`,
    customerReaction: `😀お客様の反応: ${getCustomerReaction()}`,
    bearStatus: `🐻スタッフのベアたちの様子: ${getBearStatus()}`,
    managerMessage: `🤖店長からのメッセージ: ${getManagerMessage()}`,
    specialEvent: `🎉特別なできごと！: ${getSpecialEvent()}`
  };
};




