import { GameState } from '../types';

const GAME_STATE_KEY = 'quantumBearsGameState';

export const saveGameState = (state: GameState): void => {
  localStorage.setItem(GAME_STATE_KEY, JSON.stringify(state));
};

export const loadGameState = (): GameState | null => {
  const savedState = localStorage.getItem(GAME_STATE_KEY);
  return savedState ? JSON.parse(savedState) : null;
};

export const clearGameState = (): void => {
  localStorage.removeItem(GAME_STATE_KEY);
};

// 将来的なセーブスロット機能のための関数例
export const saveToSlot = (state: GameState, slotNumber: number): void => {
  localStorage.setItem(`${GAME_STATE_KEY}_slot_${slotNumber}`, JSON.stringify(state));
};

export const loadFromSlot = (slotNumber: number): GameState | null => {
  const savedState = localStorage.getItem(`${GAME_STATE_KEY}_slot_${slotNumber}`);
  return savedState ? JSON.parse(savedState) : null;
};