import React from 'react';
import { X } from 'lucide-react';

interface CafeModalProps {
  title: string;
  content: string | JSX.Element;
  onClose: () => void;
}

export const CafeModal: React.FC<CafeModalProps> = ({ title, content, onClose }) => {
  return (
    <div className="fixed inset-0 bg-amber-900/50 backdrop-blur-sm flex items-center justify-center z-50 p-4">
      <div className="bg-gradient-to-br from-amber-100 to-amber-200 rounded-2xl p-8 shadow-2xl w-full max-w-2xl border border-amber-300 relative overflow-hidden">
        <div className="absolute inset-0 bg-opacity-10"></div>
        <div className="relative z-10">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-3xl font-bold text-amber-800 font-['Pacifico', cursive]">{title}</h2>
            <button onClick={onClose} className="text-amber-700 hover:text-amber-500 transition-colors">
              <X size={32} />
            </button>
          </div>
          <div className="space-y-4 max-h-[70vh] overflow-y-auto pr-4 custom-scrollbar">
            {typeof content === 'string' ? (
              <div dangerouslySetInnerHTML={{ __html: content }} />
            ) : (
              content
            )}
          </div>
        </div>
      </div>
    </div>
  );
};