export const checkAchievements = (state: GameState): string[] => {
  return achievements
    .filter(achievement => achievement.check(state))
    .map(achievement => achievement.id);
};

export const getAchievementDescription = (state: GameState, achievementId: string): string => {
  const achievement = achievements.find(a => a.id === achievementId);
  if (!achievement) return '';

  let description = achievement.description;
  description = description.replace('{cafeChainName}', state.config.cafeChainName);
  description = description.replace('{playerName}', state.config.playerName);
  description = description.replace('{vehicleName}', state.config.vehicleName);

  return description;
};

export const getRandomFlavorText = (achievementId: string): string => {
  const achievement = achievements.find(a => a.id === achievementId);
  if (!achievement || achievement.flavorTexts.length === 0) return '';

  const randomIndex = Math.floor(Math.random() * achievement.flavorTexts.length);
  return achievement.flavorTexts[randomIndex];
};