import { useState, useEffect, useCallback } from 'react';
import { GameState } from '../types';
import { initialGameState } from '../utils/initialState';
import { generatePlayerName, generateVehicleName, generateCafeName } from '../utils/itemGenerator';
import { bearTypes as initialBearTypes } from '../utils/bearTypes';
import { saveGameState, loadGameState } from '../utils/storage';
import { checkAchievements } from '../utils/achievements';

export const useGameState = () => {
  const [gameState, setGameState] = useState<GameState>(() => {
    const savedState = loadGameState();
    if (savedState) {
      return {
        ...savedState,
        bearTypes: initialBearTypes,
        logs: savedState.logs || [],
        config: {
          ...savedState.config,
          playerName: savedState.config.playerName || generatePlayerName(),
          vehicleName: savedState.config.vehicleName || generateVehicleName(),
          cafeChainName: savedState.config.cafeChainName || generateCafeName(),
        },
      };
    } else {
      return {
        ...initialGameState,
        bearTypes: initialBearTypes,
        logs: [],
        config: {
          ...initialGameState.config,
          playerName: generatePlayerName(),
          vehicleName: generateVehicleName(),
          cafeChainName: generateCafeName(),
        },
      };
    }
  });

  useEffect(() => {
    saveGameState(gameState);
  }, [gameState]);

  const updateGameState = useCallback((
    newState: Partial<GameState> | ((prevState: GameState) => Partial<GameState>)
  ) => {
    setGameState(prevState => {
      const updatedState = typeof newState === 'function' ? newState(prevState) : newState;
      const newGameState = { ...prevState, ...updatedState };

      // 総ベア数の計算
      const totalBears = Object.values(newGameState.bearTypes).reduce((sum, bear) => sum + bear.count, 0);
      newGameState.totalBears = totalBears;

      // 店舗数増加のロジック
      if (totalBears >= newGameState.storeCount * 5) {
        newGameState.storeCount += 1;
      }

      // アチーブメントのチェック
      const newAchievements = checkAchievements(newGameState);
      newGameState.achievements = Array.from(new Set([...newGameState.achievements, ...newAchievements]));

      return newGameState;
    });
  }, []);

  // ゲーム開始時のアチーブメント解除
  useEffect(() => {
    updateGameState(prevState => ({
      achievements: Array.from(new Set([...prevState.achievements, 'game_start']))
    }));
  }, [updateGameState]);

  return { gameState, updateGameState };
};