import React from 'react';
import { X } from 'lucide-react';
import { GameState } from '../types';
import { achievements, getAchievementDescription, getRandomFlavorText, Achievement } from '../utils/achievements';

interface AchievementModalProps {
  onClose: () => void;
  gameState: GameState;
}

export const AchievementModal: React.FC<AchievementModalProps> = ({ onClose, gameState }) => {
  return (
    <div className="bg-amber-100 rounded-xl p-6 shadow-lg w-full max-w-2xl">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold text-amber-900">Achievements</h2>
        <button onClick={onClose} className="text-amber-700 hover:text-amber-500 transition-colors">
          <X size={24} />
        </button>
      </div>
      <div className="space-y-4 max-h-[70vh] overflow-y-auto">
        {achievements.map((achievement: Achievement) => {
          const isUnlocked = gameState.achievements.includes(achievement.id);
          const description = getAchievementDescription(gameState, achievement.id);
          const flavorText = getRandomFlavorText(achievement.id);
          
          if (isUnlocked || achievement.id === 'game_start') {
            return (
              <div 
                key={achievement.id} 
                className={`p-4 rounded-lg ${isUnlocked ? 'bg-amber-300' : 'bg-amber-200'}`}
              >
                <div className="flex items-center mb-2">
                  <span className="text-2xl mr-2">{achievement.icon}</span>
                  <h3 className="text-lg font-semibold text-amber-900">
                    {achievement.title}
                  </h3>
                </div>
                <p className="text-sm text-amber-700 mb-2">{description}</p>
                <p className="text-xs text-amber-600 italic">{flavorText}</p>
                {isUnlocked && (
                  <p className="text-xs text-green-600 mt-1">Unlocked!</p>
                )}
              </div>
            );
          }
          //解除されていないachievement
          return (
            <div 
              key={achievement.id} 
              className="p-4 rounded-lg bg-amber-200"
            >
              <div className="flex items-center">
                <span className="text-2xl mr-2">❓</span>
                <h3 className="text-lg font-semibold text-amber-900">???</h3>
              </div>
              <p className="text-sm text-amber-700">This achievement is still locked.</p>
            </div>
          );
        })}
      </div>
    </div>
  );
};