// src/utils/itemGenerator.ts


// コメント: 乗り物名と店舗名のDefaultの名前
const vehicleNames = ['Quantum Cruiser', 'Nebula Explorer', 'Starlight Voyager', 'Cosmic Hopper', 'Galactic Surfer'];
const cafeNames = ['Quark Cafe', 'Neutron Star Bistro', 'Parallel Universe Diner', 'Supernova Lounge', 'Event Horizon Bar'];

export function generateVehicleName(): string {
  return vehicleNames[Math.floor(Math.random() * vehicleNames.length)];
}

export function generateCafeName(): string {
  return cafeNames[Math.floor(Math.random() * cafeNames.length)];

}


// Defaultの名前

const defaultPlayerNames = ['あなた', 'You', '名無しさん'];

export function generatePlayerName(): string {
  return defaultPlayerNames[Math.floor(Math.random() * defaultPlayerNames.length)];
}


//レアアイテム、スペシャルメニュー用
const rareItemPrefixes = ['量子', '相対性', '超弦', '暗黒物質', '多元宇宙', '錬金術', 'タイムワープ', '反物質'];
const rareItemSuffixes = ['コーヒーメーカー', 'シュガーポット', 'ミルクジャグ', 'エスプレッソマシン', 'ラテアートペン', '蒸留器', '時計', 'リアクター'];

const specialMenuPrefixes = ['時空歪曲', '量子もつれ', '超対称性', '重力波', '錬金術', 'タイムリープ', '反物質'];
const specialMenuMiddles = ['エスプレッソ', 'カプチーノ', 'ラテ', 'モカ'];
const specialMenuSuffixes = ['ア・ラ・プランク', 'デラックス', 'スープリーム', 'ウルトラ'];

export function generateRareItem(): string {
  const prefix = rareItemPrefixes[Math.floor(Math.random() * rareItemPrefixes.length)];
  const suffix = rareItemSuffixes[Math.floor(Math.random() * rareItemSuffixes.length)];
  return `${prefix}${suffix}`;
}

export function generateSpecialMenu(): string {
  const prefix = specialMenuPrefixes[Math.floor(Math.random() * specialMenuPrefixes.length)];
  const middle = specialMenuMiddles[Math.floor(Math.random() * specialMenuMiddles.length)];
  const suffix = specialMenuSuffixes[Math.floor(Math.random() * specialMenuSuffixes.length)];
  return `${prefix}${middle}${suffix}`;
}



